import React, {useState} from 'react';

import {
    Button,
    TextField,
    Dialog, DialogContent, DialogTitle, DialogActions
} from "@material-ui/core";

import {Form} from 'react-bootstrap';

function AddStrataDialog({active, setActive, field_id}) {
    const [selectedFile, setSelectedFile] = useState();
    const [fileName, setFileName] = useState("");
    const [strata, setStrata] = useState("");

    function onSubmitStrata(e){
        const formData = new FormData();
        formData.append('name', strata);
		selectedFile && formData.append('strata_file', selectedFile);
        fetch(process.env.REACT_APP_API+'strata/' + field_id + '?format=json',{
            method: 'POST',
            headers:{
                'Accept':'application/json, text/html',
                'Authorization': "Token " + localStorage.getItem('id_token')
            },
            body: formData 
        })
    };

    const handleFile = (event) => {
        setSelectedFile(event.target.files[0]);
        setFileName(event.target.files[0].name);
    }

    return (
        <>
            <Dialog open={active} onClose={setActive} maxWidth="xs" fullWidth>
                <DialogTitle> </DialogTitle>
                <Form name="addstrata" onSubmit={onSubmitStrata}>
                    <DialogContent>
                        <Form.Group>
                            <Form.Control
                            type="text"
                            id="add_strata"
                            onChange={e => setStrata(e.target.value)} 
                            placeholder="  "
                            />
                        </Form.Group>
                        <Button
                            variant="contained"
                            component="label"
                            style={{width:"100%", fontSize:"20px"}}
                            color={fileName ? "success" : "primary"}
                            >
                            {fileName ? fileName : " "}
                            <input
                                id="add-file"
                                type="file"
                                hidden
                                onChange={handleFile}
                            />
                        </Button>
                    </DialogContent>
                <DialogActions>
                    <Button type="submit" onClick={setActive} style={{outline: "none"}}></Button>
                </DialogActions>
                </Form>
            </Dialog>   
        </>
    )
}

export default AddStrataDialog;