function parametres_to_number(param) {
    if (param === "    " || param === "" || param === "" || param === "" || param === "" || param === "" || param === "  " || param === "   " || param === "    " || param === "" || param === "" || param === "") {
        return Number(1);
    }
    else if (param === "" || param === "     " || param === " " || param === " " || param === "" || param === "" || param === "     ()" || param === "") {
        return Number(0.5);
    }
    else if (param === "   ") {
        return Number(0.66);
    }
    else if (param === "   ") {
        return Number(0.33);
    } 
    else if (param === "") {
        return Number(0.8);
    }
    else if (param === "") {
        return Number(0.6);
    }
    else if (param === "   ") {
        return Number(0.4);
    }
    else if (param === "   ") {
        return Number(0.2);
    }
    else if (param === undefined || param === "    " || param === " " || param === " " || param === "" || param === "" || param === "" || param === " " || param === "     ()" || param === "     " || param === " " || param === "   " || param === "") {
        return Number(0);
    } else { return param; }
}

export function calc_value_rt_azimut(secondrow, lqc) {
    var res = 0
    for (var key in secondrow){
        if (key !== "method" && key !== "notes" && key !== "inform_for_method_id" && key !== "method_id" && key !== "index" && key !== "device_tech_condition" && key !== "distribute_palet" && key !== "distribute_support" && key !== "corresponse")
            if (lqc === "" || lqc === "")
                if (key !== "correlation")
                    res += parametres_to_number(secondrow[key]) * 9.5
                else
                    res += parametres_to_number(secondrow[key]) * 9 
            else
                res += parametres_to_number(secondrow[key]) * 6
    }
    if (lqc !== "" || lqc !== "")
        res += parametres_to_number(secondrow["device_tech_condition"]) * 24
    return res / 66 * 100;
}

export function calc_value_rt(secondrow, lqc) {
    var res = 0
    for (var key in secondrow){
        if (key !== "method" && key !== "notes" && key !== "inform_for_method_id" && key !== "method_id" && key !== "index" && key !== "device_tech_condition")
            if (lqc === "" || lqc === "")
                res += parametres_to_number(secondrow[key]) * 6.6
            else
                res += parametres_to_number(secondrow[key]) * 4.5
    }
    if (lqc !== "" || lqc !== "")
        res += parametres_to_number(secondrow["device_tech_condition"]) * 21
    return res / 66 * 100;
}

export function calc_value_memory(secondrow, lqc, koef_shod) {
    var res = 0
    if (koef_shod < 99){
        for (var key in secondrow){
            if (key !== "method" && key !== "notes" && key !== "inform_for_method_id" && key !== "method_id" && key !== "index" && key !== "device_tech_condition")
                if (lqc === "" || lqc === "")
                    res += parametres_to_number(secondrow[key]) * 5.6
                else
                    res += parametres_to_number(secondrow[key]) * 3.5 
        }
        if (lqc !== "" || lqc !== "")
            res += parametres_to_number(secondrow["device_tech_condition"]) * 21
        if (koef_shod >= 85) koef_shod = 100
        res += koef_shod / 100 * 10
    }
    return res / 66 * 100;
}

export function calc_value_memory_azimut(secondrow, lqc, koef_shod) {
    var res = 0
    if (koef_shod < 99){
        for (var key in secondrow){
            if (key !== "method" && key !== "notes" && key !== "inform_for_method_id" && key !== "method_id" && key !== "index" && key !== "device_tech_condition" && key !== "distribute_palet" && key !== "distribute_support" && key !== "corresponse")
                if (lqc === "" || lqc === "")
                    res += parametres_to_number(secondrow[key]) * 8
                else
                    res += parametres_to_number(secondrow[key]) * 5 
        }
        if (lqc !== "" || lqc !== "")
            res += parametres_to_number(secondrow["device_tech_condition"]) * 21
        if (koef_shod >= 85) koef_shod = 100
        res += koef_shod / 100 * 10
    }
    return res / 66 * 100;
}

export function calc_finish_value(rows, las, witsml, full_inf_count) {
    var res = 0
        if (las.status === true && witsml.status === true) {
            for (var i = 0; i < rows.length; i++)
                res += rows[i].method_value * 66 / 100 * rows[i].petrophysic_task
            res = res / rows.length + full_inf_count * 0.16 + las.las_file_count * 0.09 + witsml.witsml_count * 0.09
        } 
        else {
            for (var i = 0; i < rows.length; i++)
                res += rows[i].method_value * 66 / 100 * rows[i].petrophysic_task
            if (las.status === false && witsml.status === true)
                res = res / rows.length + full_inf_count * 0.22 + witsml.witsml_count * 0.12
            if (las.status === true && witsml.status === false)
            res = res / rows.length + full_inf_count * 0.22 + las.las_file_count * 0.12
        }
    return res;
}