import React, {useState, useEffect} from 'react';

import {
    Grid,
    Button,
    Typography,
    CircularProgress
  } from "@material-ui/core";

import { useHistory } from "react-router";
import MUIDataTable from "mui-datatables";
import PageTitle from "../../components/PageTitle";
import NotFound from '../../components/NotFound/NotFound';

function Reports() {
    const [reportsData, setReportsData] = useState([]);
    const constant = false;
    const history = useHistory();
    const role = localStorage.getItem('role');

    useEffect(() => {
        getReports();
    },[constant])

    const getReports = () => {
        fetch(process.env.REACT_APP_API+'quality_control/?format=json', { headers : { 
          'Content-Type': 'application/json',
          'Accept': 'application/json',
          'Authorization': "Token " + localStorage.getItem('id_token')
         }
      }) 
        .then(response=>response.json())
        .then(data=>{
            setReportsData(data);
        })
        .catch(err=> console.log(err))
      }

    const deleteReport = (report_id) => {
        fetch(process.env.REACT_APP_API+'quality_control/id/'+ report_id +'?format=json', { method: 'DELETE', headers: {'Authorization': "Token " + localStorage.getItem('id_token')} })
    }  
    return (
        <>
        <PageTitle title="" button={<Button
            variant="contained"
            size="medium"
            color="secondary"
            style={{outline: "none", backgroundColor:"#34547A", visibility: (role === "user" || role === "superuser" ? 'visible' : 'hidden')}}
            onClick={() => {history.push({pathname: "/app/checklist"})}}
            >
                  
            </Button>}/>
        <Grid container spacing={4}>
        <Grid item xs={12}>
          {reportsData.length === 0 ? <NotFound/> : 
          <MUIDataTable
            title={<Typography variant="h6">
               
            {reportsData.length === 0 && <CircularProgress size={24} style={{ marginLeft: 15, position: 'relative', top: 4 }} />}
          </Typography>}
            data={reportsData.map(item => [item.quality_control_id, item.customer, item.field, item.well, item.pie_well + " " + item.num_wellbore, item.data_type === " " ? "" : "", item.section_interval_start + " - " + item.section_interval_end, item.value])}
            columns={["", "", "", "", "", " ", "  ()", " (%)"]}
            options={{
              filterType: "textField",
              print: false,
              download: false,
              pagination: false,
              rowsPerPage: 1000000,
              textLabels: {
                body: {
                  noMatch: "  .",
                  toolTip: ""
                },
                filter: {
                  all: "",
                  title: "",
                  reset: "",
                },
                toolbar: {
                  search: "",
                  viewColumns: " ",
                    filterTable: " "
                },
                viewColumns: {
                    title: " "
                  },
                selectedRows: {
                  text: " "
                },
              },
              selectableRows: (role === "user" || role === "superuser" ? 'single' : 'none'),
              setRowProps: value => ({ style: { cursor: 'pointer' } }),
              onRowClick: (rowData, rowMeta) => {
                history.push({
                  pathname:  `/app/reports/${reportsData[rowMeta.dataIndex].quality_control_id}`,
               });
              },
              onRowsDelete: (rowMeta) => {
                if (window.confirm('     '+ reportsData[rowMeta.data[0].dataIndex].quality_control_id +'   ?'))
                    deleteReport(reportsData[rowMeta.data[0].dataIndex].quality_control_id);
                else
                    getReports();
              },
            }}
          />
          }
        </Grid>
        </Grid>
        </>
    )
}

export default Reports;