import React, {useState, useEffect} from 'react';

import {
    Button,
    Dialog, DialogContent, DialogTitle, DialogActions
} from "@material-ui/core";

import {Form} from 'react-bootstrap';

function EditWellDialog({active, setActive, strata_id, strata_name, strata_file}) {
    const [strata, setStrata] = useState("");
    const [selectedFile, setSelectedFile] = useState();
    const [fileName, setFileName] = useState("");

    function onSubmitStrata(){
        const formData = new FormData();
        formData.append('name', strata);
		formData.append('strata_file', selectedFile);
        fetch(process.env.REACT_APP_API+'strata/id/'+ strata_id + '?format=json',{
            method: 'PUT',
            headers:{
                'Accept':'application/json, text/html',
                'Authorization': "Token " + localStorage.getItem('id_token')
            },
            body: formData 
        })
    };

    const handleFile = (event) => {
        setSelectedFile(event.target.files[0]);
        setFileName(event.target.files[0].name);
    }

    useEffect(() => {
        setStrata(strata_name);
    }, [strata_name])

    useEffect(() => {
        strata_file !== null && setFileName(strata_file.slice(strata_file.lastIndexOf('/') + 1));
    }, [strata_file])

    return (
        <>
            <Dialog open={active} onClose={setActive} maxWidth="xs" fullWidth>
                <DialogTitle>  </DialogTitle>
                <Form name="editstrata" onSubmit={e => onSubmitStrata()}>
                    <DialogContent>
                        <Form.Group>
                            <Form.Label htmlFor="edit_strata"> </Form.Label>
                            <Form.Control
                            type="text"
                            id="edit_strata"
                            name="edit_strata"
                            onChange={e => setStrata(e.target.value)}
                            value={strata}
                            />
                        </Form.Group>
                        <Button
                            variant="contained"
                            component="label"
                            style={{width:"100%", fontSize:"20px"}}
                            color={fileName ? "success" : "primary"}
                            >
                            {fileName ? fileName : " "}
                            <input
                                id="add-file"
                                type="file"
                                hidden
                                onChange={handleFile}
                            />
                        </Button>
                    </DialogContent>
                <DialogActions>
                    <Button type="submit" onClick={setActive} style={{outline: "none"}}></Button>
                </DialogActions>
                </Form>
            </Dialog>   
        </>
    )
}

export default EditWellDialog;