import React, {useEffect, useState} from 'react';

import {
    Grid,
    Button,
    CircularProgress,
    Typography,
    Divider,
    Card,
    CardContent,
    TextField,
    FormControl,
    InputLabel, Select, MenuItem
  } from "@material-ui/core";
import MUIDataTable from "mui-datatables";

import {Form, Row, Col} from "react-bootstrap";

import { useHistory } from "react-router";

import CustomToolbarSelect from '../../components/Toolbar/CustomToolbarSelect';
import AddStrataDialog from '../../components/Dialogs/AddStrataDialog';
import EditStrataDialog from '../../components/Dialogs/EditStrataDialog';

import PageTitle from "../../components/PageTitle";

function Strata(props) {
    const history = useHistory();
    const role = localStorage.getItem('role');
    const constant = false;

    const [field, setField] = useState({});
    const [method, setMethod] = useState("");
    const [collectors, setCollectors] = useState(null);
    const [saturation, setSaturation] = useState(null);
    const [kp, setKp] = useState(null);
    const [kng, setKng] = useState(null);
    const [lithotype, setLithotype] = useState(null);

    const [strata, setStrata] = useState([]);
    const [strataObj, setStrataObj] = useState({
        id: null,
        name: "",
        strata_file: ""
    });
    const [selectedRows, setSelectedRows] = useState([]);

    const [allMethodsData, setAllMethodsData] = useState([]);
    const [petroData, setPetroData] = useState([]);

    const [load, setLoad] = useState(false);

    const [showModalStrata, setShowModalStrata] = useState(false);
    const [showModalEditStrata, setShowModalEditStrata] = useState(false);

    const columns = [
        {
            name: " "
        },
        {
            name: " ",
            options: {
                customBodyRender: (value, tableMeta, updateValue) => {
                    return (
                        strata[tableMeta.rowIndex].strata_file ? <Button size="medium" onClick={() => window.open(strata[tableMeta.rowIndex].strata_file, '_blank')} style={{outline: "none", width: "25%", backgroundColor:"#34547A", color:"#fff"}}> </Button>
                        : <Button size="medium" onClick={() => {setStrataObj(strata[tableMeta.rowIndex]); handleModalEditStrata();}} style={{outline: "none", backgroundColor:"#F6D106", color:"#000", width: "25%"}}> </Button>
                        );
                  }
            }
        }
    ]

    const putPetroValues = (petro_id) => {
        fetch(process.env.REACT_APP_API+'petrophysics/id/'+ petro_id +'?format=json',{
            method: 'PUT',
            headers:{
                'Accept':'application/json',
                'Content-Type':'application/json',
                'Authorization': "Token " + localStorage.getItem('id_token')
            },
            body: JSON.stringify({
                "separation_of_reservoirs": collectors === "" ? null : collectors,
                "determination_nature_saturation": saturation === "" ? null : saturation,
                "determination_Kp": kp === "" ? null : kp,
                "determination_Kng": kng === "" ? null : kng,
                "determination_lithotype": lithotype === "" ? null : lithotype
            }) 
        })
    }

    const getFieldInformation = (props) => {
        setLoad(true);
        fetch(process.env.REACT_APP_API+'fields/id/'+ props.match.params.id +'?format=json', { headers : { 
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': "Token " + localStorage.getItem('id_token')
           }
        }) 
        .then(response=>response.json())
        .then(data=>{
            setField(data);
        })
        setLoad(false);
    }

    const getStrataInformation = (props) => {
        fetch(process.env.REACT_APP_API+'strata/'+ props.match.params.id +'?format=json', { headers : { 
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': "Token " + localStorage.getItem('id_token')
           }
        }) 
        .then(response=>response.json())
        .then(data=>{
            setStrata(data);
        })
    }

    const getPetroInformation = (props, e) => {
        fetch(process.env.REACT_APP_API+'petrophysics/'+ props.match.params.id + '/' + allMethodsData.find(item => item.name === e.target.value).id +'?format=json', { headers : { 
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': "Token " + localStorage.getItem('id_token')
           }
        }) 
        .then(response=>response.json())
        .then(data=>{
            setPetroData(data);
            if (data.length !== 0) {
                setCollectors(data[0].separation_of_reservoirs)
                setSaturation(data[0].determination_nature_saturation)
                setKp(data[0].determination_Kp)
                setKng(data[0].determination_Kng)
                setLithotype(data[0].determination_lithotype)
            }  
        })
    }

    const changeName = () => {
        setStrataObj(strata[selectedRows[0]]);
        handleModalEditStrata();
    }

    const deleteStrata = (strata_id) => {
        fetch(process.env.REACT_APP_API+'strata/id/'+ strata_id +'?format=json', { method: 'DELETE', headers: {'Authorization': "Token " + localStorage.getItem('id_token')} })
    }

    const rowsDelete = () => {
        if (window.confirm('     '+ strata[selectedRows[0]].name +'?')){
          deleteStrata(strata[selectedRows[0]].id);
          strata.splice(selectedRows[0], 1);
          setSelectedRows([]);
        }
    }

    const getAllMethods = () => {
        fetch(process.env.REACT_APP_API+'methods/?format=json', { headers : { 
            'Content-Type': 'application/json',
            'Accept': 'application/json',
            'Authorization': "Token " + localStorage.getItem('id_token')
           }
        }) 
        .then(response=>response.json())
        .then(data=>{
            setAllMethodsData(data);
        })
    }

    const updateMethod = (e) => {
        setMethod(e.target.value)
        getPetroInformation(props, e)
    }

    useEffect(() => {
        getFieldInformation(props);
        getStrataInformation(props);
        getAllMethods();
    }, [constant])

    const handleModalStrata = () => {
        setShowModalStrata(!showModalStrata);
    }
    const handleModalEditStrata = () => {
        setShowModalEditStrata(!showModalEditStrata);
    }

    return (
        <>
            <AddStrataDialog active={showModalStrata} setActive={handleModalStrata} field_id={props.match.params.id}/>
            <EditStrataDialog active={showModalEditStrata} setActive={handleModalEditStrata} strata_id={strataObj.id} strata_name={strataObj.name} strata_file={strataObj.strata_file}/>
            <Grid item xs={12}>
                <PageTitle title="  "/>
                <Form.Group as={Row} style={{paddingBottom:"1rem"}}>
                        <Col xs="3">
                            <FormControl fullWidth variant="standard">
                                <InputLabel id="method"></InputLabel>
                                    <Select labelId="method" name="method" label="" size="small" value={method} onChange={e => updateMethod(e)}>
                                        <MenuItem hidden disabled></MenuItem>
                                        {allMethodsData.map(item => <MenuItem key={item.name} value={item.name} style={{fontSize:"20px"}}>{item.name}</MenuItem>)}
                                    </Select>
                            </FormControl>
                        </Col>
                        <Col xs="9"></Col>
                </Form.Group>
                <Card sx={{ minWidth: 275 }}>
                    <CardContent>
                    <Form.Group as={Row} xs="10">
                        <Col xs="2">
                        <TextField type="number" InputLabelProps={{ shrink: true }} fullWidth id="collectors" label=" " variant="standard" value={collectors === null ? "" : collectors} onChange={e => setCollectors(e.target.value)}/>
                        </Col>
                        <Col xs="2">
                        <TextField type="number" InputLabelProps={{ shrink: true }} fullWidth id="saturation" label="  " variant="standard" value={saturation === null ? "" : saturation} onChange={e => setSaturation(e.target.value)}/>
                        </Col>
                        <Col xs="2">
                        <TextField type="number" InputLabelProps={{ shrink: true }} fullWidth id="kp" label=" " variant="standard" value={kp === null ? "" : kp} onChange={e => setKp(e.target.value)}/>
                        </Col>
                        <Col xs="2">
                        <TextField type="number" InputLabelProps={{ shrink: true }} fullWidth id="kng" label=" " variant="standard" value={kng === null ? "" : kng} onChange={e => setKng(e.target.value)}/>
                        </Col>
                        <Col xs="2">
                        <TextField type="number" InputLabelProps={{ shrink: true }} fullWidth id="lithotype" label=" " variant="standard" value={lithotype === null ? "" : lithotype} onChange={e => setLithotype(e.target.value)}/>
                        </Col>
                        <Col xs="2" style={{textAlign:"center"}}>
                            <Button onClick={() => petroData.length !== 0 && putPetroValues(petroData[0].id)} style={{outline: "none", display: "inline-block", marginTop: "0.75rem", height:"100%", width:"85%", backgroundColor:"#34547A", color:"#fff", visibility: (role === "user" || role === "superuser" ? 'visible' : 'hidden')}}></Button>
                        </Col>
                    </Form.Group>
                    </CardContent>
                </Card>
                <Divider/>
            </Grid>
            <PageTitle title={field.name !== undefined ? "  " + field.name : "  "} button={<Button
            variant="contained"
            size="medium"
            color="secondary"
            style={{outline: "none", backgroundColor:"#34547A", visibility: (role === "user" || role === "superuser" ? 'visible' : 'hidden')}}
            onClick={handleModalStrata}
            >
                 
            </Button>} />
            <Grid container spacing={4}>
                <Grid item xs={12}>
                    <MUIDataTable
                        title={<Typography variant="h6">
                           
                        {load && <CircularProgress size={24} style={{ marginLeft: 15, position: 'relative', top: 4 }} />}
                    </Typography>}
                        data={strata.map(item => [item.name])}
                        columns={columns}
                        options={{
                        print: false,
                        filter: false,
                        download: false,
                        rowsPerPage: 100,
                        rowsPerPageOptions: [100,500,1000],
                        viewColumns: "false",
                        rowsSelected: selectedRows,
                        textLabels: {
                            body: {
                              noMatch: "  .",
                              toolTip: ""
                            },
                            filter: {
                              all: "",
                              title: "",
                              reset: "",
                            },
                            toolbar: {
                              search: "",
                              viewColumns: " ",
                                filterTable: " "
                            },
                            viewColumns: {
                                title: " "
                              },
                            selectedRows: {
                              text: " "
                            },
                            pagination: {
                                next: " ",
                                previous: " ",
                                rowsPerPage: "  :",
                                displayRows: "",
                              },
                          },
                        onRowSelectionChange: (rowsSelectedData, allRows, rowsSelected) => {
                            setSelectedRows(rowsSelected);
                        },
                        customToolbarSelect: (selectedRows, displayData, setSelectedRows) => (
                            <CustomToolbarSelect selectedRows={selectedRows} displayData={displayData} setSelectedRows={setSelectedRows} onChangeName={changeName} onRowsDelete={rowsDelete}/>
                        ),
                        selectableRows: (role === "user" || role === "superuser" ? 'single' : 'none'),
                        }}
                    />
                </Grid>
                <Grid item xs={12}>
                <Button color="secondary" variant="contained" onClick={() => {history.push({pathname: "/app/customers"})}} style={{outline: "none", backgroundColor:"#34547A"}}> </Button>
                </Grid>
            </Grid>
        </>
    )
}

export default Strata
